body {
  padding: 0;
  margin: 0;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: #262626
}
.container{
  position: relative;
  background: linear-gradient(45deg,#69d9e2, #003da2);   
  width: 325px;
  height: 550px;
  border-radius: 7.5px;
  overflow: hidden;
}
h2 {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0px;
  text-align: center;
  line-height: 40px;
  margin: 0px;
  box-shadow: 0px 3px 20px #262626;
  color: #fbfbfb;
  font-size: 20px;
  font-family: 'Roboto Condensed', sans-serif;
}
#addBox {
  position: relative;
  display: block;
  background: #003da2;
  width: 300px;
  height: 35px;
  margin: 15px auto;
  border-radius: 2px;
}
input {
  width: 250px;
  background: none;
  border: none;
  outline: none;
  font-family: 'Roboto Condensed', sans-serif;
  background: none;
  padding: 5px;
  margin-top: 3px;
  color: #fbfbfb;
  margin-left: 36px;
  font-size: 16px;
}
#txt::placeholder {
  color: #fbfbfb
}

#plus {
  position: absolute;
  width: 25px;
  height: 25px;
  cursor: pointer;
  margin: 5px 0px 5px 10px;
}
#plus::before{
  position: absolute;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f067";
  color: #fbfbfb;
  font-size: 24px;
  transition: .4s;
}
#plus:hover::before{
  margin-top: -3px;
  margin-left: -3px;
  font-size: 30px;
}
#list {
  width: 300px;
  height: 400px;
  margin: 10px auto;
  overflow: scroll;
}
.box {
  position: relative;
  display: block;
  width: 300px;
  overflow: scroll;
  height: 45px;
  margin: 5px 0px 5px 0px;
  background: #fbfbfb;
  /* opacity: .92; */
  box-sizing: border-box;
  transition: .5s;
  max-height: 100%;
  border-radius: 3px;
}
.box::-webkit-scrollbar {
  display: none;
}
#list::-webkit-scrollbar {
  display: none;
}
.x{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
  width: 20px;
  height: 20px;
}
.x:before{
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f00d";
  font-size: 20px;
  color: #262626;
  cursor: pointer;
  transition: .5s;
}
.x:hover:before{
  color: #bc2626
}
.txtBox {
  /* font-family: 'Roboto Condensed', sans-serif; */
  /* position: absolute;
  left: 20px;
  top: 12px; */
  font-size: 17px;
  background-color: #c4ec64;
  -moz-text-decoration-color: black;
}
.box.active {
  max-height: 0%;
  opacity: 0;
  margin: 0;
}
.box.checked {
  background: #b6ff00;
}